
@font-face {
  font-family: mingTi;
  src: url('/static/fonts/WenYue-GuDianMingChaoTi-NC-W5.ttf');
}
#app {
  line-height: 1.5;
  font-family: mingTi;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#app h1 {
  font-family: mingTi;
}
#app .preview-item {
  right: 100%;
  opacity: 0;
}
#app.active .preview-item {
  right: 0;
  opacity: 1;
}
.loading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background: #ecdec5 repeat;
}
.loading-logo {
  width: 300px;
  height: 300px;
}
.loading-logo .row {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.loading-logo .row:nth-of-type(2) {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.loading-logo .circle {
  width: 60px;
  height: 60px;
  -webkit-animation: loading 2s linear infinite;
          animation: loading 2s linear infinite;
  background: url(assets/img/loading.png) center no-repeat;
  background-size: 100%;
}
.loading-logo .logo {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: url(assets/img/xuanwu.png) center no-repeat;
  background-size: cover;
  /*animation: loading 4s linear infinite;*/
}
.loading-logo .row:nth-of-type(2) .logo {
  background-image: url(assets/img/baihu.png);
}
.loading-logo .row:nth-of-type(2) .logo:nth-child(3) {
  background-image: url(assets/img/qinglong.png);
}
.loading-logo .row:nth-of-type(3) .logo {
  background-image: url(assets/img/zhuque.png);
}
@-webkit-keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.load-leave-to {
  opacity: 0;
}
.load-leave-active {
  transition: opacity .5s;
}
.navbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
}
.navbar .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 10px;
}
.navbar a {
  display: block;
  color: #fff;
}
.nav-left {
  position: relative;
}
.nav-left a {
  padding: 10px;
}
.nav-left:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.nav-right {
  position: relative;
}
.nav-right .nav-btn {
  display: none;
  outline: none;
}
.nav-right .menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nav-right .menu a {
  padding: 15px;
  width: 14px;
  line-height: 22px;
  white-space: normal;
  transition: all .2s;
}
.nav-right .menu a:hover {
  color: #000;
  background-color: gainsboro;
}
.nav-enter .nav-left {
  left: -75px;
  opacity: 0;
}
.nav-enter .nav-right {
  top: -75px;
  opacity: 0;
}
.nav-enter-active {
  transition: left 1s;
}
.nav-enter-active .nav-left {
  transition: left 1s, opacity 2s;
}
.nav-enter-active .nav-right {
  transition: top 1s, opacity 2s;
}
.nav-enter-to .nav-left {
  opacity: 1;
  left: 0;
}
.nav-enter-to .nav-right {
  opacity: 1;
  top: 0;
}
.header {
  margin-bottom: 10px;
  position: relative;
  background: no-repeat center;
  background-color: gray;
  background-size: cover;
}
.header .content {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 400px;
  padding: 80px 15px 90px;
}
.header .content h1 {
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  font-size: 50px;
  color: #fff;
}
.header .content h1 span {
  display: inline-block;
  margin: 10px 0;
}
.header .wisdom {
  position: absolute;
  opacity: 0;
  left: 20px;
  bottom: 20px;
  max-height: 200px;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  letter-spacing: 2px;
  color: #fff;
  font-size: 16px;
}
.header .wisdom.active {
  opacity: .6;
  transition: opacity 2s 1s;
}
.header-appear {
  opacity: 0;
}
.header-appear span {
  opacity: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
.header-enter {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  opacity: 0;
}
.header-enter-active {
  transition: opacity 1.3s .3s, -webkit-transform 1s .3s;
  transition: transform 1s .3s, opacity 1.3s .3s;
  transition: transform 1s .3s, opacity 1.3s .3s, -webkit-transform 1s .3s;
}
.header-enter-active span {
  transition: opacity 1.3s .3s, -webkit-transform 1s .3s;
  transition: transform 1s .3s, opacity 1.3s .3s;
  transition: transform 1s .3s, opacity 1.3s .3s, -webkit-transform 1s .3s;
}
.header-enter-active span:nth-of-type(2) {
  transition-delay: .5s;
}
.header-enter-active span:nth-of-type(3) {
  transition-delay: .7s;
}
.header-leave-active {
  transition: all .3s;
}
.header-leave-to {
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  opacity: 0;
}
.audio-container {
  margin-bottom: 15px;
}
.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  position: relative;
}
footer {
  margin: 20px 0 30px;
}
footer.out {
  position: fixed;
  top: 100%;
}
.links {
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}
.links h5 {
  margin-bottom: 10px;
}
.links ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.links li {
  padding: 0 7px;
}
.links li a {
  color: #bfbfbf;
}
.links li a:hover {
  color: #0085a1;
  text-decoration: underline;
}
.copy {
  margin-top: 30px;
  text-align: center;
}
.copy a {
  color: #000;
}
.switch-enter {
  -webkit-transform: translateX(103%);
          transform: translateX(103%);
}
.switch-leave,
.switch-enter-to {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.switch-enter-active {
  position: absolute;
  top: 42px;
  left: 15px;
  right: 15px;
}
.switch-leave-active,
.switch-enter-active {
  transition: all 1.3s;
}
.switch-leave-to {
  -webkit-transform: translateX(-103%);
          transform: translateX(-103%);
}
@media (min-width: 768px) {
.container {
    width: 750px;
}
}
@media (min-width: 992px) {
.container {
    width: 970px;
}
}
@media (min-width: 1200px) {
.container {
    width: 1170px;
}
}
@media only screen and (max-width: 767px) {
.navbar .content {
    padding: 0;
}
.nav-right .nav-btn {
    display: block;
    padding: 10px;
    color: #fff;
    border: none;
    background: none;
    font-size: 20px;
}
.nav-right .nav-btn:hover {
    color: #D2D2D2;
}
.nav-right .menu {
    position: absolute;
    top: 35px;
    right: 10px;
    background-color: #FFFFFF;
    box-shadow: rgba(0, 0, 0, 0.117647) 0 1px 6px, rgba(0, 0, 0, 0.239216) 0 1px 4px;
    border-radius: 2px;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-transform-origin: top right;
            transform-origin: top right;
    transition: -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
    transition: transform 500ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 500ms cubic-bezier(0.23, 1, 0.32, 1);
}
.nav-right .menu.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}
.nav-right .menu.active a {
    opacity: 1;
}
.nav-right .menu.active a:hover {
    border-radius: 2px;
}
.nav-right .menu.out {
    opacity: 0;
}
.nav-right .menu a {
    opacity: 0;
    transition: all .2s, opacity .2s .2s;
    color: #777;
}
.header .content {
    height: 250px;
    padding: 60px 15px 40px;
}
.header .content h1 {
    font-size: 36px;
    line-height: 50px;
}
.header .content h1 span {
    margin: 7px 0;
}
.header .wisdom {
    left: 15px;
    bottom: 10px;
    max-height: 150px;
    font-size: 14px;
}
}
::-webkit-scrollbar {
  width: 0px;
}



