
.article {
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
  font-size: 13px;
  line-height: 18px;
  color: #737373;
}
.article .title,
.article .describe {
  text-align: center;
}
.article .describe {
  font-style: italic;
  color: #a3a3a3;
}
.article video {
  display: block;
  width: 100%;
  margin: 20px auto 0;
  max-width: 665px;
}
.article a {
  color: #0069d6;
}
.article a:hover {
  color: #0050a3;
}
.article h1,
.article h2,
.article h3,
.article h4,
.article h5,
.article h6 {
  color: #404040;
  line-height: 36px;
}
.article h1 {
  margin-bottom: 18px;
  font-size: 30px;
}
.article h2 {
  font-size: 24px;
}
.article h3 {
  font-size: 18px;
}
.article h4 {
  font-size: 16px;
}
.article h5 {
  font-size: 14px;
}
.article h6 {
  font-size: 13px;
}
.article hr {
  margin: 0 0 19px;
  border: 0;
  border-bottom: 1px solid #ccc;
}
.article blockquote {
  padding: 13px 13px 21px 15px;
  margin-bottom: 18px;
  font-family: georgia, serif;
  font-style: italic;
}
.article blockquote:before {
  content: "\201C";
  font-size: 40px;
  margin-left: -10px;
  font-family: georgia, serif;
  color: #eee;
}
.article blockquote p {
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
  margin-bottom: 0;
  font-style: italic;
}



